import {
  robotMsgMutation
} from "../mutation-types.js";

const state = {
  robotmsg: [
    // 
    {
      message: "hi , ",
      user: "robot"
    }
  ]
};

const mutations = {
  //
  [robotMsgMutation](state, data) {
    state.robotmsg.push(data);
  }
}

export default {
  state,
  mutations
}



