import {
  newFriendMutation, friendReqTipsMutation
} from "../mutation-types.js";

const state = {
  systemName: 'XXX',
  newFriend: [], //
  tabTips: { //tab 
    addFriendReq: "" // 0 1
  }

};

const mutations = {
  //
  [newFriendMutation](state, data) {
    state.newFriend = data;
  },
  //
  [friendReqTipsMutation](state, data) {
    if (data) {
      state.tabTips.addFriendReq = "tips";
    } else {
      state.tabTips.addFriendReq = "";
    }
  }
}

export default {
  state,
  mutations
}



