<template>
<!--   -->
<div class="wrapper">
	<Header goback='true' userInfo='true' :chatTitle="someOneInfoGetter.name"></Header>
	<ul>
		<li v-for="item in privateDetail">
			<ChatItem v-if="fromUserInfo.user_id === item.from_user" :href="item.from_user" :img="item.avator" me="true" :msg="item.message" :name="item.name" :time="item.time"></ChatItem>
			<ChatItem v-else :img="item.avator" :msg="item.message" :href=" item.from_user " :name="item.name" :time="item.time"></ChatItem>
		</li>
	</ul>
	<div class="input-msg">
		<textarea v-model="inputMsg" @keydown.enter.prevent="sendMessage" ref="message"></textarea>
		<p class="btn" :class="{'enable':inputMsg!=''}" @click="sendMessage">{{btnInfo}}</p>
	</div>
</div>
</template>

<script>
import Header from '../components/Header.vue'
import ChatItem from '../components/ChatItem.vue'
import axios from "axios"
import {	toNomalTime} from "../utils/common";
import { mapGetters, mapActions } from 'vuex';
export default {
	components: {
		Header,
		ChatItem
	},

	data() {
		return {
			inputMsg: '',
			privateDetail: [], //
			toUserInfo: { //
				to_user: '',
				avator: '',
				sex: '',
				place: '',
				status: ''
			},
			isMyFriend: false, //
			isHisFriend: false, //
			fromUserInfo: {}, //
			btnInfo: ""
		}
	},

	computed: {
		...mapGetters([
			'someOneInfoGetter'
		])
	},

	watch: {
		privateDetail() {
			this.refresh();
		}
	},

	methods: {
    ...mapActions(["getPrivateDetail","isFriendJudge","queryUserInfoSpecial","savePrivateMsg"]),
		//
		getPrivateMsg() {
      let params = {
        to_user: this.toUserInfo.to_user
      }
      this.getPrivateDetail(params).then(res => {
        if (res.success) {
          this.privateDetail = res.data.privateDetail;
          if (this.privateDetail.length == 0) return
          this.privateDetail.forEach(element => {
            element.time = toNomalTime(element.time);
            element.message = element.message.split(':')[1];
          });
        }
      }).catch(err => {
        const errorMsg = err.response.error
        this.$message({
          message: errorMsg,
          type: "error"
        });
      })
		},
		//
		sendMessage() {
			if (this.inputMsg.trim() == '') return
			if (!this.isMyFriend) {
				this.$message({
					message: 'tata',
					type: "error"
				});
				return
			}
			if (!this.isHisFriend) {
				this.$message({
					message: 'tata',
					type: "error"
				});
				return
			}
			this.sendMsgBySocket();
			this.saveMsgByDB();
		},
		//socket
		sendMsgBySocket() {
			const data = {
				from_user: this.fromUserInfo.user_id, //id
				to_user: this.toUserInfo.to_user, //id
				name: this.fromUserInfo.name, //
				avator: this.fromUserInfo.avator, //
				message: this.inputMsg, //
				type: 'private',
				status: '1', // 0 1
				time: toNomalTime((new Date()).getTime()), //
			};
      socketWeb.emit('sendPrivateMsg', data)
			this.$store.commit('updateListMutation', data);
		},
		//
		saveMsgByDB() {
			const data = {
				from_user: this.fromUserInfo.user_id, //id
				to_user: this.toUserInfo.to_user, //id
				name: this.fromUserInfo.name, //
				avator: this.fromUserInfo.avator, //
				message: this.inputMsg, //
				status: '1', // 0 1
				time: toNomalTime((new Date()).getTime()), //
			}
			// 
      this.savePrivateMsg(data)	.then(res => {
        if(res){
          this.inputMsg = '';
          // 
          this.privateDetail.push(data);
        }
      }).catch(err => {
        const errorMsg = err.response.error
        this.$message({
          message: errorMsg,
          type: "error"
        });
      })
		},
		// socket
		getMsgBySocket() {
      socketWeb.removeAllListeners('getPrivateMsg');
      socketWeb.on('getPrivateMsg', (data) => {
				//soket soket  
				// if(data.to_user != this.fromUserInfo.user_id) return
				//soket  return

				// console.log(data.from_user, '!=', this.toUserInfo.to_user)
				// 	//soket vuex 
				if (data.from_user != this.toUserInfo.to_user) {
					data.chatOfNow = false;
					this.$store.commit('updateListMutation', data)
					return
				} else {
					//soket vuex 
					data.chatOfNow = true;
					this.$store.commit('updateListMutation', data)
				}
				//
				this.privateDetail.push(data);
			})
		},
		// 
		isFriend() {
      let params = {
        other_user_id: this.toUserInfo.to_user
      }
      this.isFriendJudge(params).then(res => {
        if (res) {
          this.isMyFriend = res.data.isMyFriend.length !== 0 ? true : false;
          this.isHisFriend = res.data.isHisFriend.length !== 0 ? true : false;
        }
      }).catch(err => {
        const errorMsg = err.response.error
        this.$message({
          message: errorMsg,
          type: "error"
        });
      })
		},
		//
		resetUnred() {
			this.$store.commit('resetUnredMutation', this.toUserInfo.to_user)
		},
		// 
		refresh() {
			setTimeout(() => {
				window.scrollTo(0, document.body.scrollHeight + 10000)
			}, 0)
		}
	},
	created() {
		this.toUserInfo.to_user = this.$route.params.user_id;
		this.fromUserInfo = JSON.parse(localStorage.getItem("HappyChatUserInfo"));
		this.isFriend();
		this.resetUnred();
		this.getPrivateMsg();
		this.getMsgBySocket();
		this.queryUserInfoSpecial({user_id: this.toUserInfo.to_user})
		// this.$store.dispatch('someOneInfoAction', this.toUserInfo.to_user)
	}
}
</script>

<style lang="scss" scoped>
@import "../assets/css/chat.scss";
</style>



